import cultureManager from "@/CultureManager";
import {ProductCategoryDiscriminator} from "@/data/ProductCategoryDiscriminator";

export class ProductCategory {
    id: number;
    nameEst: string;
    nameEng: string;
    onDisplay: boolean;
    discriminator: ProductCategoryDiscriminator;
    
    constructor(id: number, nameEst: string, nameEng: string, onDisplay: boolean, discriminator: ProductCategoryDiscriminator) {
        this.id = id;
        this.nameEst = nameEst;
        this.nameEng = nameEng;
        this.onDisplay = onDisplay;
        this.discriminator = discriminator;
    }
    
    localizedName() : string {
        return cultureManager.isEstonian ? this.nameEst : this.nameEng;
    }
}

class ProductCategoriesProvider {
  private categories: ProductCategory[] = [
      new ProductCategory(1, "Kleidid", "Dresses", true, ProductCategoryDiscriminator.Dresses),
      new ProductCategory(2, "Kimonod", "Kimonos", true, ProductCategoryDiscriminator.Kimonos),
      new ProductCategory(3, "Pluusid", "Tops", true, ProductCategoryDiscriminator.Tops),
      new ProductCategory(4, "Seelikud", "Skirts", true, ProductCategoryDiscriminator.Skirts),
      new ProductCategory(5, "Püksid", "Pants", true, ProductCategoryDiscriminator.Pants),
      new ProductCategory(6, "Pükstükid", "Jumpsuits", true, ProductCategoryDiscriminator.Jumpsuits),
      new ProductCategory(7, "Muu", "Other", true, ProductCategoryDiscriminator.Other),
      new ProductCategory(8, "Valmistooted", "Ready-to-wear", true, ProductCategoryDiscriminator.ReadyToWear),
      new ProductCategory(9, "Üksikeksemplarid", "One of a kind pieces", true, ProductCategoryDiscriminator.Unique)
  ];

  getAllCategories(): ProductCategory[] {
    return this.categories;
  }
  
  getAllVisibleCategories(): ProductCategory[] {
    return this.categories.filter(c => c.onDisplay);
  }
  
  getCategoryById(id: number): ProductCategory | undefined {
    return this.categories.find(c => c.id === id);
  }
  
  getCategoryByName(name: string): ProductCategory | undefined {
    return this.categories.find(c => c.nameEng === name);
  }
}

export const productCategoriesProvider = new ProductCategoriesProvider();