import cultureManager from "../CultureManager";
import descriptionData from "@/data/productDescriptions.json";

export class ProductDescription {
    productId!: number;
    descriptionEst!: string;
    descriptionEng!: string;
    picturePaths!: string[];
    materialModelDescriptionEst!: string;
    materialModelDescriptionEng!: string;
    sizeDescriptionEst!: string;
    sizeDescriptionEng!: string;
    
    constructor(data?: any) {
        if (data) {
            this.productId = data["productId"] !== undefined ? data["productId"] : null;
            this.descriptionEst = data["descriptionEst"] !== undefined ? data["descriptionEst"] : null;
            this.descriptionEng = data["descriptionEng"] !== undefined ? data["descriptionEng"] : null;
            this.picturePaths = data["picturePaths"] !== undefined ? data["picturePaths"] : [];
            this.materialModelDescriptionEst = data["materialModelDescriptionEst"] !== undefined ? data["materialModelDescriptionEst"] : null;
            this.materialModelDescriptionEng = data["materialModelDescriptionEng"] !== undefined ? data["materialModelDescriptionEng"] : null;
            this.sizeDescriptionEst = data["sizeDescriptionEst"] !== undefined ? data["sizeDescriptionEst"] : null;
            this.sizeDescriptionEng = data["sizeDescriptionEng"] !== undefined ? data["sizeDescriptionEng"] : null;
        }
    }
    
    localizedDescription(): string {
        return cultureManager.isEstonian ? this.descriptionEst : this.descriptionEng;
    }
    
    localizedMaterialModelDescription(): string {
        return cultureManager.isEstonian ? this.materialModelDescriptionEst : this.materialModelDescriptionEng;
    }
    
    localizedSizeDescription(): string {
        return cultureManager.isEstonian ? this.sizeDescriptionEst : this.sizeDescriptionEng;
    }
    
    getPicturePathForIndex(index: number): string {
        return require(`@/assets/${this.picturePaths[index]}`);
    }
}

class ProductDescriptionProvider {
    constructor() {
        this.productDescriptions = descriptionData.map((data: any) => new ProductDescription(data));
    }
    private productDescriptions: ProductDescription[];
    getProductDescription(productId: number): ProductDescription | undefined {
        return this.productDescriptions.find((d) => d.productId === productId);
    }
}

export const productDescriptionProvider = new ProductDescriptionProvider();